.pipelineContainer {
    display: flex;
    justify-content: center;


    .pipeline {
        margin: 5px;
        padding: 5px !important;
        position: fixed;
        z-index: 10000;
        color: white;
        display: flex;
        flex-flow: row nowrap;
        justify-content: left;
        align-items: left;
        max-width: 90dvw;
        padding: 20px;
        gap: 20px;
        overflow-x: scroll;
        scrollbar-color: transparent transparent;
        scrollbar-width: none;
        scroll-behavior: smooth;
        border-right: 2px lime solid;
        border-left: 2px lime solid;
    }

    .pipelineJob {
        transition: 1s;
        border: 2px lime solid;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0 10px;
        border-radius: 50px;
        gap: 0;
        cursor: pointer;
        width: 220px;
        white-space: nowrap;
        backdrop-filter: blur(2px);
        box-shadow: 0 0 10px lime, inset 0 0 10px lime;
    }

    .pipelineJobDetails {
        color: lime;
        transition: .75s;
        margin-right: 10px;
        opacity: 1;
        width: 150px;

        div {
            transition: .5s;
        }

        h1 {
            font-family: 'Courier New', Courier, monospace;
            font-size: 1.1em;
            text-shadow: 0 0 10px lime;
        }

        p {
            font-family: 'Courier New', Courier, monospace;
            text-shadow: 0 0 10px lime;
        }
    }

    .pipelineIcon {
        transition: .5s;
        width: 48px;
        height: 48px;
        margin: 5px;
    }

    .runningIcon {
        filter: blur(1.1px) saturate(150%) contrast(200%)
    }

    .checkIcon {
        filter: blur(1.1px) saturate(1500%)
    }

    .pauseIcon {
        filter: blur(1.1px) saturate(150%)
    }
}

@media only screen and (max-width: 768px) {
    .pipelineContainer {
        .pipeline {
            max-width: 100dvw;
        }
    }
}
