.contactContainer {
    display: flex;
    flex-direction: column;
    width: 30dvw;
    min-width: 30dvw;

    h1 {
        font-size: 3em;
    }

    .textAreaCounter {
        display: flex;
        align-items: end;
    }

    .sendButtonContainer {
        align-items: end;
        margin-top: 5%;
    }

    form {
        div {
            display: flex;
            flex-direction: column;
        }

        input {
            color: lime;
            background: transparent;
            border: 2px lime solid;
            border-radius: 5px;
            caret-shape: underscore;
            font-size: 1.2em;
            text-shadow: 0 0 10px lime;
            filter: blur(0.5px);

        }

        textarea {
            color: lime;
            background: transparent;
            border: 2px lime solid;
            border-radius: 5px;
            caret-shape: underscore;
            font-size: 1.2em;
            filter: blur(0.5px);
            height: 30dvh;
        }

        label {
            margin-top: 10px;
            font-size: 1.2em;
        }
    }
}

@media only screen and (max-width: 768px) {

    .contactContainer {
        display: flex;
        flex-direction: column;
        width: 80dvw;

        h1 {
            font-size: 1.2em;
        }

        div {
            gap: 10px;
            max-width: 450px;

            input {
                font-size: 1.2em;
                height: 40px;
                filter: blur(0);
            }

            textarea {
                height: 20dvh;
                filter: blur(0);
            }

            label {
                margin-top: 10px;
                font-size: 1em;
            }
        }
    }
}