.textContainer {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 40px;

    h2 {
        font-size: 5em;
        text-transform: uppercase;
        filter: blur(1px);
    }

    h1 {
        font-weight: normal;
        font-size: 5em;
        filter: blur(1px);
    }

    div {
        display: flex;
        flex-direction: row;
        gap: 20px;

        button {
            font-size: 1em;
        }
    }
}

@media only screen and (max-width: 768px) {
    .textContainer {
        h1 {
            font-size: 2.5em;
        }

        h2 {
            font-size: 2.1em;
        }

        div {
            justify-content: space-evenly;
            gap: 0;
            height: 10dvh;
            button {
                height: 100%;
                font-size: 1em;
            }
        }
    
    }
}