.overlay.hide {
    visibility: hidden;
    pointer-events: none;
}

.overlay {
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100dvh;
    width: 100dvw;
    z-index: 1000000;
    color: lime;
    background: black;

    .loadingBarOutline {
        border: 10px lime solid;
        width: 60dvw;
        height: 15dvh;
        display: flex;
        align-items: center;
        justify-content: left;

        .loadingBar {
            margin: 5px;
            height: 95%;
            background: lime;
        }
    }

    .lettersContainer {
        display: flex;
        flex-direction: row;
    }

    h1 {
        font-size: 15em;
    }
}

@media only screen and (max-width: 768px) {
    .overlay {
        .loadingBarOutline {
            border: 2px lime solid;
            width: 80dvw;
            height: 5dvh;

            .loadingBar {
                margin: 0 2px;
                height: 90%;
            }
        }

        h1 {
            font-size: 2em;
        }
    }
}