.buttonsContainer {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;

    .socialsButtonContainer {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        min-width: 150px;
    }

    .button {
        transition: .5s;
        cursor: pointer;

        p {
            color: inherit;
            font-weight: bold;
            text-shadow: none;
        }
    }

    .linkedInButton {
        color: #2196f3;
    }

    .linkedInButton:hover {
        color: #90caf9;
    }

    .instagramButton {
        color: #ec407a;
    }

    .instagramButton:hover {
        color: #f48fb1;
    }

    .youtubeButton {
        color: #d32f2f;
    }

    .youtubeButton:hover {
        color: #e57373;
    }

    .githubButton {
        color: white;
    }

    .githubButton:hover {
        color: lightslategray;
    }


}


.spotifyFrame {
    border: none;
    border-radius: 12px;
}

@media only screen and (max-width: 768px) {
    .buttonsContainer {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 1fr 1fr;
        gap: 10px;
        width: 60dvw;

        .socialsButtonContainer {
            min-width: 100px;
        }
    }

    .spotifyFrame {
        border-radius: 12px;
        height: 30dvh;
        // max-width: 60dvw;
    }
}