.businessCard {
    display: flex;
    flex-direction: column;
    width: 450px;

    .titleAndButton {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 20px;
        gap: 25px;
    }

    .linkedInButton {
        transition: .5s;
        color: #2196f3;
        cursor: pointer;
    }

    .linkedInButton:hover {
        color: #90caf9;
    }
}

@media only screen and (max-width: 768px) {
    .businessCard {
        width: 40dvw;
    }
}