.windowWrapper {
    display: flex;
    width: unset;
    position: absolute;
    color: lime;

    .windowContainer {
        border: 2px lime solid;
        backdrop-filter: blur(10px) brightness(80%);
        position: relative;
        border-radius: 5px;


        img {
            border-radius: 5px;
        }

        .windowTitle {
            max-width: inherit;
            border: 1px lime solid;
            padding: 10px;
            cursor: grab;
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: 20px;

            .windowButtons {
                display: flex;
                gap: 10px;
                
                .windowButton {
                    transition: .5s;
                    border-radius: 5px;
                    margin: 2px;
                    cursor: pointer;
                }

                .windowButton:hover {
                    background: rgb(44, 44, 44)
                }
            }
        }
    }

    .fileContainer {
        display: flex;
        cursor: pointer;
    }

    .file {
        transition: .25s;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        font-size: 9em;
        color: lime;
        
        .fileName {
            white-space: nowrap;
            font-size: .15em;
        }
    }

    .file:hover {
        scale: 1.05;
    }
    
    .file:active {
        scale: 0.9;
        color: green;
        text-shadow: 0 0 10px green;
    }
}


@media only screen and (max-width: 768px) {
    .windowWrapper {
        width: 80dvw;
        max-height: 70dvh;

        .windowContainer {
            max-width: 500px;

            .windowTitle {
                h1 {
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    max-width: 300px;
                }
            }
        }

        .fileContainer {
            font-size: 5em;

        }

        .file {

            .fileName {
                font-size: 1em;
                max-width: 20dvw;
            }
        }
    }
}

