* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Courier New', Courier, monospace;
  text-shadow: 0 0 10px lime;

  ::selection {
    color: black;
    background: lightgray;
    text-shadow: none;
  }

  --background-color: #333333
}

section {
  height: 100vh;
  min-height: stretch;
  scroll-snap-align: center;
  background: linear-gradient(to top, black, black, var(--background-color), var(--background-color));
  background-size: cover;
  background-size: 100% 1px;
}

html {
  scroll-snap-type: y mandatory;
  scroll-behavior: smooth;
  scrollbar-color: lime var(--background-color);
  overscroll-behavior-y: none;
}


button {
  transition: .5s;
  padding: 0 10px;
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  background: transparent;
  border: 2px lime solid;
  height: 40px;
  max-width: 20%;
  cursor: pointer;
  font-size: 1.5em;
  border-radius: 5px;
  font-weight: bold;
  filter: blur(0.5px);
  color: lime;
}

button:hover {
  background: lime;
  color: black;
}

.note {
  p {
    margin-bottom: 20px;
    font-size: 1.4em;
    font-weight: bold;
  }

  h1 {
    margin-bottom: 20px;
    font-size: 2em;
  }

  ul {
    font-weight: normal;
    margin-left: 20px;
  }
}


@media only screen and (max-width: 768px) {
  * {
    font-size: 16px;
  }

  img {
    max-width: 250px;
  }

  button {
    max-width: none;
  }

  .note {
    p {
      margin-bottom: 10px;
      font-size: 1em;
      font-weight: bold;
    }

    h1 {
      margin-bottom: 20px;
      font-size: 1.1em;
    }

    ul {
      font-weight: normal;
      margin-left: 20px;
    }
  }
}